module.exports = "/* General container styles */\ndiv {\n  font-family: \"Arial\", sans-serif;\n  background-color: #f4f6f9;\n  padding: 30px;\n  max-width: 800px;\n  margin: 40px auto;\n  border-radius: 15px;\n  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);\n  -webkit-animation: fadeIn 1.5s ease-in-out;\n          animation: fadeIn 1.5s ease-in-out;\n}\n/* Header Styles */\nh2 {\n  text-align: center;\n  color: #3f51b5;\n  font-size: 26px;\n  margin-bottom: 30px;\n  font-weight: 700;\n  -webkit-animation: fadeInUp 1.5s ease-in-out;\n          animation: fadeInUp 1.5s ease-in-out;\n}\n/* Upload Section */\ninput[type=file] {\n  display: none;\n}\n.file-upload-container {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  border: 2px solid #4CAF50;\n  border-radius: 50px;\n  background-color: #fff;\n  padding: 12px 30px;\n  cursor: pointer;\n  transition: all 0.3s ease;\n  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);\n  -webkit-animation: fadeIn 1.5s ease-in-out;\n          animation: fadeIn 1.5s ease-in-out;\n}\n.file-upload-container:hover {\n  background-color: #f2f2f2;\n  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);\n}\n.file-upload-container .file-name {\n  font-size: 16px;\n  font-weight: 500;\n  color: #333;\n}\n.file-upload-container .file-icon {\n  margin-right: 10px;\n  font-size: 20px;\n  color: #4CAF50;\n}\n/* Form Section */\nform {\n  display: flex;\n  flex-wrap: wrap;\n  /* Allows the input fields to wrap into multiple rows */\n  gap: 20px;\n  max-width: 600px;\n  margin: 0 auto;\n  -webkit-animation: fadeInUp 1.5s ease-in-out;\n          animation: fadeInUp 1.5s ease-in-out;\n}\n/* Form Row for Input Fields */\n.form-row {\n  display: flex;\n  flex: 2;\n  /* Allow each form row to take equal space */\n  flex-basis: calc(50% - 10px);\n  /* 50% width for two inputs per row, with gap between them */\n  gap: 10px;\n}\n/* Input Field Styles */\ninput, textarea {\n  width: 100%;\n  padding: 14px;\n  border-radius: 8px;\n  border: 2px solid #ddd;\n  font-size: 16px;\n  transition: border 0.3s ease;\n  background-color: #fff;\n}\ninput:focus, textarea:focus {\n  border: 2px solid #4CAF50;\n  outline: none;\n}\n/* Label Styles */\nlabel {\n  font-size: 14px;\n  font-weight: 600;\n  color: #333;\n  margin-bottom: 8px;\n  display: block;\n  text-transform: uppercase;\n}\n/* Button Styles */\nbutton {\n  padding: 12px 25px;\n  background-color: #4CAF50;\n  color: white;\n  border: none;\n  border-radius: 8px;\n  font-size: 18px;\n  cursor: pointer;\n  transition: background-color 0.3s ease;\n  align-self: center;\n}\nbutton:hover {\n  background-color: #45a049;\n}\n/* Animation for Section Appearance */\n@-webkit-keyframes fadeIn {\n  0% {\n    opacity: 0;\n  }\n  100% {\n    opacity: 1;\n  }\n}\n@keyframes fadeIn {\n  0% {\n    opacity: 0;\n  }\n  100% {\n    opacity: 1;\n  }\n}\n@-webkit-keyframes fadeInUp {\n  0% {\n    opacity: 0;\n    -webkit-transform: translateY(30px);\n            transform: translateY(30px);\n  }\n  100% {\n    opacity: 1;\n    -webkit-transform: translateY(0);\n            transform: translateY(0);\n  }\n}\n@keyframes fadeInUp {\n  0% {\n    opacity: 0;\n    -webkit-transform: translateY(30px);\n            transform: translateY(30px);\n  }\n  100% {\n    opacity: 1;\n    -webkit-transform: translateY(0);\n            transform: translateY(0);\n  }\n}\n/* Responsive Styles */\n@media (max-width: 600px) {\n  h2 {\n    font-size: 20px;\n  }\n\n  form {\n    width: 100%;\n  }\n\n  .file-upload-container {\n    width: 100%;\n    font-size: 14px;\n  }\n\n  input, textarea {\n    font-size: 14px;\n  }\n\n  .form-row {\n    flex: 1 1 100%;\n    /* Stack the fields on small screens */\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"