slateX(-100%);\n            transform: translateX(-100%);\n  }\n  100% {\n    -webkit-transform: translateX(0);\n            transform: translateX(0);\n  }\n}\n.company-info h3 {\n  text-align: center;\n  font-size: 1.8rem;\n  color: #2c3e50;\n  margin-bottom: 15px;\n}\n.company-info p {\n  font-size: 1rem;\n  color: #7f8c8d;\n  line-height: 1.6;\n}\n.map-container {\n  margin-top: 40px;\n  text-align: center;\n}\n.map-container iframe {\n  border-radius: 10px;\n  width: 100%;\n  height: 300px;\n  border: none;\n  transition: -webkit-transform 0.3s ease;\n  transition: transform 0.3s ease;\n  transition: transform 0.3s ease, -webkit-transform 0.3s ease;\n}\n.map-container iframe:hover {\n  -webkit-transform: scale(1.05);\n          transform: scale(1.05);\n}\n/* Responsive Styles */\n@media (max-width: 768px) {\n  .contact-container {\n    padding: 20px;\n  }\n\n  h2 {\n    font-size: 2rem;\n  }\n\n  input {\n    font-size: 0.9rem;\n    padding: 10px;\n  }\n\n  button {\n    font-size: 1rem;\n    padding: 10px 20px;\n  }\n\n  .company-info h3 {\n    font-size: 1.5rem;\n  }\n\n  .company-info p {\n    font-size: 0.95rem;\n  }\n\n  .map-container iframe {\n    height: 250px;\n  }\n}\n@media (max-width: 480px) {\n  .contact-container {\n    padding: 15px;\n  }\n\n  h2 {\n    font-size: 1.8rem;\n  }\n\n  input {\n    font-size: 0.85rem;\n    padding: 8px;\n  }\n\n  button {\n    font-size: 0.95rem;\n    padding: 10px;\n  }\n\n  .company-info h3 {\n    font-size: 1.3rem;\n  }\n\n  .company-info p {\n    font-size: 0.85rem;\n  }\n\n  .map-container iframe {\n    height: 200px;\n  }\n}\n.custom-hr {\n  border: none;\n  text-align: center;\n  overflow: visible;\n  margin: 40px 0;\n  height: 1px;\n  background-color: #bdc3c7;\n  position: relative;\n  transition: background-color 0.4s ease;\n}\n.custom-hr:after {\n  content: \"Our Location\";\n  display: inline-block;\n  position: relative;\n  top: -14px;\n  padding: 0 10px;\n  background: white;\n  font-size: 16px;\n  color